.passage {
  position: absolute;
  z-index: 1;
}
.passage_index {
  position: absolute;
  height: 100px;
  width: 100px;
  padding: 12px;
  transform: translate(-50%, -50%);
  background: rgba(66, 166, 266, 0.781);
  border: none;
  cursor: default;
}
.top {
  z-index: 5 !important;
}
.options {
  position: absolute;
  opacity: 0;
  z-index: 5;
  pointer-events: none;
  width: 100px;
  height: 30px;
  background: white;
  transform: translate(-50%, 77px);
}
.options > button {
  object-fit: contain;
  border: none;
  width: 30px;
  height: 100%;
  background-size: cover;
  outline: none;
  cursor: pointer;
}
.delete_btn {
  background-image: url("./images/delete.svg");
  background-color: white;
}
.line {
  position: absolute;
  bottom: 0;
  right: 0;
  width: 58px;
  height: 52px;
  border: 2px solid cyan;
  opacity: 0;
}
.line_inner {
  position: absolute;
  display: flex;

  justify-content: space-around;
  align-items: center;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transform-origin: top;
  transform: translate(7px, -3px) rotateZ(48deg) scale(0.5, 1);
}
.line_inner > div {
  width: 2px;
  height: 20px;
  background: cyan;
}
.line_inner > div:nth-child(1) {
  height: 5px;
}
.line_inner > div:nth-child(2) {
  height: 25px;
}
.line_inner > div:nth-child(3) {
  height: 45px;
}
.visible {
  pointer-events: fill;
  opacity: 1;
}
.empty_mode {
  clip-path: polygon(0 0, 0 100%, 35% 100%, 50% 100%, 100% 55%, 100% 0);
}
.passage_text {
  margin: 0;
  cursor: default;
  word-wrap: break-word;
}
.title {
  font-size: 16px;
}
.textile {
  font-weight: 500;
  font-size: 12px;
  word-wrap: break-word;
}
